export default {
	data() {
		return {
			swiperheight: 500,
			tabIndex: 0,
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100);
				this.swiperheight = height;
			}
		});
	},
	methods: {
		tabtap(index) {
			this.tabIndex = index;
		},
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
	}
}
